/* global define, require, Handlebars, Backbone, Marionette, $, _ */

define('core/dropdown/views/behaviors/CustomAnchorBehavior',[ 'core/libApi' ],
    function () {
        'use strict';

        var classes = {
            ANCHOR: 'dropdown__anchor'
        };

        /**
         *  Marionette.Behavior    .    options  
         *     behavior (.  Marionette).
         * @name CustomAnchorBehavior
         * @memberof module:core.dropdown.views.behaviors
         * @class Behavior    {@link module:core.dropdown.views.PopoutView PopoutView}   <code>customAnchor: true</code>.
         *    <code>buttonView</code> View    behavior,       (, anchor).
         * @constructor
         * @extends Marionette.Behavior
         * @param {Object} options  
         * @param {String} [options.anchor] jQuery-  DOM-,     .
         *                                    ,    View.
         * @param {Marionette.View} view View     Behavior
         * */

        return Marionette.Behavior.extend(/** @lends module:core.dropdown.views.behaviors.CustomAnchorBehavior.prototype */ {
            initialize: function (options, view) {
            },

            onRender: function () {
                var $el;
                if (this.options.anchor) {
                    $el = this.$(this.options.anchor);
                } else {
                    $el = this.$el;
                }
                $el.addClass(classes.ANCHOR);
            }
        });
    });

